const mission = `Le Théâtre Point Virgule est une compagnie de théâtre qui crée et produit des productions théâtrales ayant pour but de susciter chez son public des réflexions sur des enjeux sociaux.
Fondée en 2019 par Émilie Bossé, Alexandra Fontaine et Éric-Olivier Bossé, le Théâtre Point Virgule offrira sa première production Le confessionnal, dans les écoles secondaires, en janvier 2020; une pièce de théâtre destinée aux adolescents de dernier cycle qui traite de l’anxiété reliée au passage à l’âge adulte.
Le Théâtre Point Virgule a à cœur de produire des productions théâtrales qui font bouger les choses, sensibilisent son public sur des problématiques sociales et qui détruisent les tabous; tout en donnant accès à des œuvres théâtrales de qualité à des publics qui n’y ont pas toujours accès.
Dans un monde où les institutions sociales sont de plus en plus fragiles, nous offrons une alternative aux ressources existantes avec une expérience artistique complète : Des créations théâtrales suivies de médiations d’intervention animées par des professionnels pour outiller nos publics sur les problématiques sociales que nous abordons.
`;

export default mission;